.slider__inner {
    margin-top: 70px;
}

.slider {
    position: relative;
}

.slider__img {
    background: url(../img/slider_bg01.jpg) no-repeat center / cover;
    opacity: 0.8;
    transition: all .5s ease;
}

.slider__img:hover{
    opacity: 1;
}

.slider__img .desc {
    width: 1160px;
    margin: 0 auto;
    padding: 100px 20px;
}

.slider__img .desc span {
    display: inline-block;
    font-size: 16px;
    background-color: #fff;
    padding: 1px 14px 0px 14px;
    border-radius: 30px;
    text-transform: uppercase;
    margin-bottom: 10px;
}

.slider__img .desc h3 {
    font-size: 110px;
    font-weight: 300;
    line-height: 1;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 16px;
    margin-left: -8px;
}

.slider__img .desc p {
    font-size: 20px;
    font-weight: 300;
    color: #fff;
    line-height: 1.35;
    margin-bottom: 130px;
}

.slider__img .desc .btn a {
    display: inline-block;
    font-size: 16px;
    background-color: #fff;
    padding: 11px 50px;
}

.slider__img .desc .btn a.black {
    background: #000;
    color: #fff;
}

.slider__arrow  a{
    position: absolute;
    top: 50%;
    width: 30px;
    height: 56px;
    background-image: url(../img/slider_icon.svg);
    transform: translateY(-50%);
}

.slider__arrow  a.left {
    left: 20px;
}
.slider__arrow  a.right {
    background-position: -50px 0px;
    right: 20px;
}

.slider__dot {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: 25px;
}

.slider__dot a{
    display: inline-block;
    width: 16px;
    height: 16px;
    background-image: url(../img/slider_icon.svg);
    background-position: -20px -70px;
}

.slider__dot a.active{
    background-position: 0 -70px;
}

.slider__dot a.play{
    background-position: -40px -70px;
}

.slider__dot a.stop{
    background-position: -60px -70px;
}

/* swiper */
.swiper-slide {
    background: url(../img/slider_bg01.jpg) no-repeat center / cover;
    height: 500px;
}

.swiper-slide .desc {
    width: 1160px;
    margin: 0 auto;
    padding: 100px 20px;
    box-sizing: border-box;
}

.swiper-slide .desc span {
    display: inline-block;
    font-size: 16px;
    background-color: #fff;
    padding: 1px 14px 0px 14px;
    border-radius: 30px;
    text-transform: uppercase;
    margin-bottom: 10px;
}

.swiper-slide .desc h3 {
    font-size: 110px;
    font-weight: 600;
    line-height: 1;
    color: #fff;
    text-transform: uppercase;
    margin-bottom: 16px;
    margin-left: -8px;
}

.swiper-slide .desc p {
    font-size: 20px;
    font-weight: 600;
    color: #fff;
    line-height: 1.35;
    margin-bottom: 130px;
}

.swiper-slide .desc .btn a {
    display: inline-block;
    font-size: 16px;
    background-color: #fff;
    padding: 11px 50px;
}

.swiper-slide .desc .btn a.black {
    background: #000;
    color: #fff;
}

.swiper-button-next,
.swiper-button-prev {
    width: 30px !important;
    height: 56px !important;
    background-image: url(../img/slider_icon.svg);
}
.swiper-button-next {
    background-position: -50px 0;
}

.swiper-button-next:after,
.swiper-button-prev:after {
    font-size: 0;
    opacity: 0;
}

.swiper-pagination-bullet {
    width: 16px !important;
    height: 16px !important;
    background-image: url(../img/slider_icon.svg) !important;
    background-position: -20px -70px !important;
    background-color: transparent !important;
    opacity: 1 !important;
}

.swiper-pagination-bullet-active {
    background-position: 0 -70px !important;
}
.swiper-button {
    position: absolute;
    left: 50%;
    margin-left: 40px;
    bottom: 24px;
    z-index: 1000;
    display: flex;
    cursor: pointer;
}
.swiper-button-stop {
    background-image: url(../img/slider_icon.svg);
    background-position: -60px -70px;
    width: 16px;
    height: 16px;
}
.swiper-button-play {
    background-image: url(../img/slider_icon.svg);
    background-position: -40px -70px;
    width: 16px;
    height: 16px;
}
/* media */
@media(max-width : 960px) {
    .swiper-slide .desc {
        width: 100%;
        text-align: center;
    }

    .swiper-slide .desc h3 {
        font-size: 70px;
    }
}

@media(max-width : 600px) {
    .swiper-slide .desc {
        padding: 80px 0;
    }
    .swiper-slide .desc h3 {
        font-size: 40px;
    }

    .swiper-slide .desc p {
        font-size: 16px;
        margin-bottom: 50px;
    }

    .swiper-slide .desc .btn a {
        padding: 10px 25px;
    }
}